@import '../../style/variables.scss';

.randomchar {
    display: grid;
    grid-template-columns: 1fr 1fr;
    box-shadow: 5px 5px 20px rgba(0, 0, 0, 0.25);
    @media (max-width: 900px){
        grid-template-columns: 1fr;
    }
    &__block {
        padding: 40px 35px;
        display: grid;
        grid-template-columns: 180px auto;
        column-gap: 30px;
        @media (max-width: 768.98px){
            padding: 15px; 
            grid-template-columns: 1fr;
        }
    }
    &__img {
        position: relative;
        padding-bottom: 50%;
        @media (max-width: 900px){
           padding-bottom: 100%;   
        }
        @media (max-width: 768.98px){
            padding-bottom: 70%;
        }
        img{
            position: absolute;width: 100%;height: 100%;top: 0;left: 0;
            object-fit: cover;
        }
    }
    &__info {
        row-gap: 10px;
        padding-top: 3px;
        display: flex;
        flex-direction: column;
    }
    &__name {
        font-weight: bold;
        font-size: 22px;
        line-height: 29px;
        text-transform: uppercase;
        @media (max-width: 768.98px){
            font-size: 16px; 
        }
    }
    &__descr {
        flex: 1 1 auto;
        font-size: 14px;
        line-height: 18px;
    }
    &__btns {
        display: flex;
        gap: 30px;
    }
    &__static {
        padding: 40px 35px;
        background-color: $dark;
        position: relative;
        button {
            margin-top: 13px;
            position: relative;
            z-index: 5;
        }
        @media (max-width: 768.98px){
            padding: 15px; 
        }
    }
    &__title {
        font-weight: bold;
        font-size: 24px;
        line-height: 32px;
        letter-spacing: -0.045em;
        color: #FFFFFF;
        position: relative;
        z-index: 5;
        @media (max-width: 1100px){
            font-size: 18px; 
        }
        @media (max-width: 900px){
            font-size: 24px; 
        }
        @media (max-width: 768.98px){
            font-size: 16px; 
        }
        &:nth-child(2) {
            margin-top: 33px;
        }
    }
    &__decoration {
        position: absolute;
        bottom: 14px;
        right: -37px;
    }
}