@import '../../style/variables.scss';

.char {
    &__info {
        padding: 25px;
        box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.25);
        position: relative;
        z-index: 5;
        background-color: #fff;
        &-name {
            flex: 1 1 auto;
            font-weight: bold;
            font-size: 22px;
            line-height: 29px;
            text-transform: uppercase;
            @media (max-width: 500px){
                margin-bottom: 20px;
            }
        }
        .skeleton {
            margin-top: 30px;
        }
    }
    &__image {
        position: relative;
        @media (max-width: 500px){
            padding-bottom: 100%;
        }
        img{
            position: absolute;width: 100%;height: 100%;top: 0;left: 0;
            object-fit: cover;
        }
    }
    &__basics {
        display: grid;
        grid-template-columns: 150px auto;
        gap: 25px;
        @media (max-width: 500px){
            grid-template-columns: 1fr;
        }

        & > div {
            display: flex;
            flex-direction: column;
        }
    }
    &__btns {
        display: flex;
        gap: 35px;
    }
    &__descr {
        margin-top: 15px;
        font-size: 14px;
        line-height: 18px;
    }
    &__comics {
        font-weight: bold;
        font-size: 18px;
        line-height: 24px;
        margin-top: 10px;
        &-list {
            position: relative;
            margin-top: 10px;
        }
        &-item {
            width: 100%;
            padding: 0px 10px;
            line-height: 25px;
            box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
            margin-top: 10px;
        }
    }
    &__select {
        font-weight: bold;
        font-size: 18px;
        line-height: 24px;
        text-align: center;
    }

}